'use client';

import * as React from 'react';
import Image from 'next/image';
import {
  Dialog,
  DialogContent,
  DialogTrigger,
} from '@/components/ui/dialog';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

const galleryItems = [
  {
    vehicle: 'Porsche 911 GT3',
    before: { src: 'https://placehold.co/600x400.png', hint: 'dirty porsche' },
    after: { src: 'https://placehold.co/600x400.png', hint: 'clean porsche' },
  },
  {
    vehicle: 'Ferrari 488',
    before: { src: 'https://placehold.co/600x400.png', hint: 'dirty ferrari' },
    after: { src: 'https://placehold.co/600x400.png', hint: 'clean ferrari' },
  },
  {
    vehicle: 'Lamborghini Huracan',
    before: { src: 'https://placehold.co/600x400.png', hint: 'dirty lamborghini' },
    after: { src: 'https://placehold.co/600x400.png', hint: 'clean lamborghini' },
  },
    {
    vehicle: 'McLaren 720S',
    before: { src: 'https://placehold.co/600x400.png', hint: 'dirty mclaren' },
    after: { src: 'https://placehold.co/600x400.png', hint: 'clean mclaren' },
  },
];

export default function GallerySection() {
  const [selectedImage, setSelectedImage] = React.useState<string | null>(null);

  return (
    <section id="galeria" className="py-20 sm:py-32 bg-card">
      <div className="container">
        <div className="text-center">
          <h2 className="font-headline text-4xl font-bold uppercase text-primary md:text-5xl">
            Galería
          </h2>
          <p className="mt-4 max-w-2xl mx-auto text-muted-foreground">
            Resultados que hablan por sí mismos. La transformación es nuestra especialidad.
          </p>
        </div>

        <Dialog open={!!selectedImage} onOpenChange={(isOpen) => !isOpen && setSelectedImage(null)}>
          <div className="mt-12 grid grid-cols-1 gap-8 md:grid-cols-2">
            {galleryItems.map((item, index) => (
              <Card key={index} className="overflow-hidden bg-background">
                <CardHeader>
                  <CardTitle className="font-body text-2xl font-bold text-center text-white">
                    {item.vehicle}
                  </CardTitle>
                </CardHeader>
                <CardContent className="grid grid-cols-2 gap-4 p-4">
                  <DialogTrigger asChild>
                    <div className="relative cursor-pointer" onClick={() => setSelectedImage(item.before.src)}>
                      <Image
                        src={item.before.src}
                        alt={`Antes - ${item.vehicle}`}
                        width={600}
                        height={400}
                        className="rounded-md object-cover"
                        data-ai-hint={item.before.hint}
                      />
                      <div className="absolute inset-0 flex items-center justify-center bg-black/50 opacity-0 transition-opacity hover:opacity-100">
                        <span className="font-bold text-white">ANTES</span>
                      </div>
                    </div>
                  </DialogTrigger>
                  <DialogTrigger asChild>
                    <div className="relative cursor-pointer" onClick={() => setSelectedImage(item.after.src)}>
                      <Image
                        src={item.after.src}
                        alt={`Después - ${item.vehicle}`}
                        width={600}
                        height={400}
                        className="rounded-md object-cover"
                        data-ai-hint={item.after.hint}
                      />
                      <div className="absolute inset-0 flex items-center justify-center bg-black/50 opacity-0 transition-opacity hover:opacity-100">
                        <span className="font-bold text-white">DESPUÉS</span>
                      </div>
                    </div>
                  </DialogTrigger>
                </CardContent>
              </Card>
            ))}
          </div>
          {selectedImage && (
            <DialogContent className="max-w-4xl p-0 border-0">
              <Image
                src={selectedImage}
                alt="Imagen ampliada"
                width={1200}
                height={800}
                className="h-auto w-full rounded-lg object-contain"
              />
            </DialogContent>
          )}
        </Dialog>
      </div>
    </section>
  );
}
